
import Header from "@/components/common/Header";
import VideoPlayer from "@/components/common/VideoPlayer";
import SwiperImg from "@/components/common/SwiperImgGoodDetails";
import { getDetail, CollectionGoods, SaveShopping } from "@/servers/api";
import Divider from "@/components/common/DividedArea";
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { setTimeout } from "timers";

export default {
directives: {
TransferDom
},
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "商品详情",
showLeftBack: true,
showRightMore: false
},
selModel: "",
showPopup: false,
showPopupOption: false,
goodsData: {},
selName: "",
selOption: "",
selService: "",
collectState: true,
SwiperImgData: {
ImgList: [],
index: 0,
dotsPosition: "center",
loop: true,
auto: true,
height: "160px"
}
};
},

components: {
Header,
VideoPlayer,
SwiperImg,
Divider,
Group,
Cell,
CellFormPreview,
CellBox,
Badge,
TransferDom,
Popup,
Checker,
CheckerItem,
XButton
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {
this.getGoodsDetailsInfo();
},

methods: {
blockChainInfoFn() {
let id = this.$route.query.id;
let flag = this.goodsData.is_forty;
let rule = this.goodsData.rule;
if (flag) {
this.$router.push(
"/blockinfoindex?id=" + id + "&flag=" + flag + "&rule=" + rule
);
} else {
this.$vux.toast.text("数据上链中...", "middle");
setTimeout(() => {
this.$vux.toast.hide();
}, 1000);
}
},

confirmOrder() {
this.$router.push("/confirmorder?id=" + this.$route.query.id);
},

addShoppingCart() {
SaveShopping({
gid: this.goodsData.id
})
.then(res => {
if (res.result === 1) {
this.$vux.toast.show({
type: "success",
text: "添加成功",
time: 1000
});
}
})
.catch(err => {
console.log(err);
});
},

collectGoods() {
this.collectState = !this.collectState;
let type = this.collectState === false ? 0 : 1;
this.collectionGoodsMethod(type);
},
collectionGoodsMethod(type) {
CollectionGoods({
goodsid: this.goodsData.id,
type: 1
})
.then(res => {
if (res.result === 1) {
if (type === 1) {
this.$vux.toast.show({
type: "success",
text: "收藏成功",
time: 1000
});
} else {
this.$vux.toast.show({
type: "success",
text: "取消收藏",
time: 1000
});
}
}
})
.catch(err => {
console.log(err);
});
},
setForItem(name, id) {
this.selName = name;
setTimeout(() => {
this.showPopup = false;
}, 300);
},
selOptionFn2() {
this.showPopupOption = true;
},
selServiceFn() {
this.showPopupOption = true;
},
selOptionFn() {
this.showPopup = true;
},
getGoodsDetailsInfo() {
getDetail({
goods_id: this.$route.query.id
})
.then(res => {
console.log(res);
let arr = [];
if (res.result === 1) {
if (res.data.favorite) {
this.collectState = true;
} else {
this.collectState = false;
}
if (res.data.thumb_url.length) {
for (let i = 0; i < res.data.thumb_url.length; i++) {
arr.push({
img: res.data.thumb_url[i]
});
}
this.SwiperImgData.ImgList = JSON.parse(JSON.stringify(arr));
}
this.$nextTick(() => {
this.goodsData = res.data;
});
}
})
.catch(err => {
console.log(err);
});
}
},

watch: {}
};



